
.Home-item-icon {
  background-image: url("./images/icons@2x.png");
  background-size: 192px 224px;
  width: 30px;
  height: 30px;
  display: inline-block;
}
.Home-item-icon-keysignature {
  background-position: 0px 0;
}
.Home-item-icon-note {
  background-position: -32px 0;
}
.Home-item-icon-interval{
  background-position: -64px 0;
}
.Home-item-icon-scale {
  background-position: -96px 0;
}
.Home-item-icon-chord {
  background-position: -128px 0;
}

a {
  text-decoration: none;
  color: #3083fb;
}

.Home-button {
  margin: 1px;
  float: left;
  width: 42px;
  height: 42px;
  line-height:46px; 
  color: blue;
  text-align: center;
  background: #e0e0e0;
  border-radius: 50%;
}

.Home-button-hi {
  /*box-shadow: 0 0 5px #909090;*/
  background: #3083fb;
  color: white;
}
.Home-icon {
  width: 160px;
  padding: 2px;
  margin: 2px;
  float: left;
  text-align: center;
}
.Home-icon-hi {
  -webkit-box-shadow: 0 0 5px #3083fb;
          box-shadow: 0 0 5px #3083fb;
  /*box-shadow: 0 0 5px #909090;*/
}



